body {
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
  
}

.square {
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
  height: 100vh; /* Full viewport height */

  background-color: rgb(8, 10, 4);
  border: 2px  ;
  
  float: left;
  font-size: 150px;
  font-weight: bold;
  line-height: 200px;

  margin: 3px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 200px;
  height: 200px;
  border-radius:4px;
  

  box-shadow: 2px 2px 5px #A1E3F9,
  0px 0px 15px #A1E3F9 inset;
  transition: transform 0.3s ease-in-out, box-shadow 0.5s ease-in-out;
}

.square:hover {
  transform: scale(1.1);
  box-shadow: 3px 3px 10px #333333,
              0px 0px 20px #d8d8d8 inset;
              cursor: pointer;
}

@media (max-width: 1218px ) {
  .square {

    font-size: 50px;
    width: 100px;
    height: 100px;
    
  }
}
@media (max-width: 800px) {
  .square{
    font-size: 50px;
    width: 67px;
    height: 67px;

  }
}
@media (max-width: 435px) {
  .square {
    font-size: 50px;
    width: 67px;
    height: 67px;

  }
}





.board-row:after {
  clear: both;
  content: '';
  display: table;

}
.game-board {
  margin: 10px 15px;
  margin-left: 20%;

}

.status {
  
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
  
  
  background-color: #333333;
  border-radius: 5px;
  align-items: center;

  font-size: 35px;
  

  margin: 1px 30%;
  margin-bottom: 15px;
  padding: 4px;
  cursor: default;
  
  text-shadow: 0px 0px 10px white ,
              0px 0px 20px white ,
              0px 0px 40px white ,
              0px 0px 80px white ;
}


.game-info {
  margin-left: 20px;
  align-items: center;
  background-color: #FEEFAD;
  border: 2px solid black;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0px 0px 10px white,
  0px 0px 20px white ,
  0px 0px 40px white ;
  display: grid;

}
.game-info h3{
  font-size: 34px;
  margin: 2px;
  margin-left: 5px;
  
}
.game-info button{
  outline: none;
  border: none;
  font-size: 25px;
  background-color: #68D2E8;
  border-radius:  5px;
  padding: 3px;
  width: 100%;
  margin-bottom: 15px;
  cursor: pointer;
  text-shadow: 0px 0px 10px white ;
  transition: transform 0.3s ease-in-out, box-shadow 0.5s ease-in-out;
}
.game-info button:hover{
  transform: scale(1.1);
}
.game-info ol{
  padding: 20px;
  font-size: 25px;
}
@media (max-width: 1218px ) {
  .game-info h3{
    font-size: 30px;
    margin-bottom: 1px;
  }
  .game-info button {
    font-size: 20px;
  }
  .status{
    font-size: 25px;
  }
}
@media (max-width: 800px) {
  .game-info h3{
    font-size: 20px;
    margin-bottom: 1px;
  }
  .game-info button {
    font-size: 15px;
    margin-bottom: 3px;
  }
  .status{
    font-size: 20px;
    
    margin: 1px 10%;
  }
  .game-info ol{
    font-size: 15px;

  }
}


.game{
  
  background-color: rgb(51, 51, 51);
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 10px;
  padding: 10px;
}

@media (max-width: 435px) {
  .game{
    display: flex;
    flex-wrap: wrap;
    margin-top: 25px;
  }
  .game-info h3{
    font-size: 10px;
    margin-bottom: 1px;
  }
  .game-info button {
    font-size: 10px;
    margin-bottom: 3px;
    margin: 2px;
  }
  .status{
    font-size: 20px;
    
  }
  .game-info ol{
    font-size: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between ;
    margin: 1px;
    padding: 5px;
  }
}
.xx {
  border: 3px solid #68D2E8;
  color: #68D2E8;
}

.oo {
  border: 3px solid #FDDE55;
  color: #FDDE55;
}



